import React from "react";
import "./App.css";
import "../node_modules/highlight.js/styles/monokai-sublime.css";
import "react-input-range/lib/css/index.css";
import { OnSiteApp } from "./OnSite.jsx";
import { YoutubeWithAssistantApp } from "./YoutubeWithAssistantApp";
import { GifApp } from "./GifApp";
import { GifWithAssistantApp } from "./GifWithAssistantApp";
import { YoutubeApp } from "./YoutubeApp";
import { CodeAssistantClient } from "./CodeAssistantClient";
import VimeoWithCodeAssistant from "./VimeoWithCodeAssistant";
import { VimeoApp } from "./Vimeo";

export default function App({
  fileChangesURL,
  videoURL,
  gifURL,
  type,
  showFrameNumber,
  layout,
  videoId,
  wrapperClassName,
}) {
  const res = {
    codeAssistantYoutube: () => (
      <YoutubeWithAssistantApp
        layout={layout}
        fileChangesURL={fileChangesURL}
        videoId={videoId}
        wrapperClassName={wrapperClassName}
      />
    ),
    codeAssistantVimeo: () => (
      <VimeoWithCodeAssistant
        layout={layout}
        fileChangesURL={fileChangesURL}
        videoId={videoId}
        wrapperClassName={wrapperClassName}
      />
    ),
    youtube: () => <YoutubeApp videoId={videoId} />,
    vimeo: () => <VimeoApp videoId={videoId} />,
    onSite: () => <OnSiteApp layout={layout} fileChangesURL={fileChangesURL} videoURL={videoURL} />,
    gif: () => <GifApp gifUrl={gifURL} />,
    codeAssistantGif: () => (
      <GifWithAssistantApp
        fileChangesURL={fileChangesURL}
        gifUrl={gifURL}
        showFrameNumber={showFrameNumber}
        layout={layout}
      />
    ),
    codeAssistantClient: () => <CodeAssistantClient />,
  }[type];
  return res ? res() : `Invalid type (${type})`;
}

export function toExtension(fileName) {
  const fName = fileName ? fileName.substr(fileName.lastIndexOf(".") + 1) : "plaintext";
  return fName === "txt" ? "plaintext" : fName;
}
