import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";

(function () {
  const roots = document.getElementsByClassName("kodseged");
  if (!roots.length) {
    return;
  }

  const urlParams = new URLSearchParams(window.location.search);
  const isFullPage = urlParams.get("isFullPageClientApp");

  if (isFullPage) {
    Array.from(roots)
      .filter((root) => root.dataset.type === "codeAssistantClient")
      .forEach(renderRoot);
  } else {
    Array.from(roots)
      .filter((root) => root.dataset.type !== "codeAssistantClient")
      .forEach(renderRoot);
  }

  function renderRoot(root) {
    ReactDOM.render(
      <React.StrictMode>
        <App
          wrapperClassName={root.dataset?.wrapperclassname}
          fileChangesURL={root.dataset?.filechangesurl}
          videoURL={root.dataset?.videourl}
          videoId={root.dataset?.videoid}
          type={root.dataset?.type}
          gifURL={root.dataset?.gifurl}
          showFrameNumber={Boolean(root.dataset?.showframenumber)}
          layout={root.dataset?.layout}
        />
      </React.StrictMode>,
      root
    );
  }

  // If you want your app to work offline and load faster, you can change
  // unregister() to register() below. Note this comes with some pitfalls.
  // Learn more about service workers: https://bit.ly/CRA-PWA
  serviceWorker.unregister();
})();
