import React, { Fragment, useEffect, useRef, useState } from "react";
import Vimeo from "@u-wave/react-vimeo";
import { toExtension } from "./App";
import moment from "moment";
import "./App.css";
import { ControlButtons, Loading, Renderer } from "./YoutubeWithAssistantApp";

export default function VimeoWithCodeAssistant({ fileChangesURL, videoId, layout, wrapperClassName }) {
  const video = useRef();
  const [isInit, setInit] = useState();
  const [videoStateChanged, setVideoStateChanged] = useState(0);
  const [currentLayout, setCurrentLayout] = useState();
  const [explicitelyActive, setExplicitelyActive] = useState("");
  const [isVisible, setVisible] = useState(false);
  const [fontSize, setFontSize] = useState(16);

  let channel;
  try {
    channel = new BroadcastChannel("codeAssistant");
  } catch {
    channel = {
      onmessage: () => {},
      postMessage: () => {},
      close: () => {},
      noop: true,
    };
  }
  React.useMemo(() => channel, []);

  React.useEffect(() => {
    channel.onmessage = (message) => {
      if (message.data.event === "clientClosed") {
        setVisible(false);
      }
    };
    return channel.close;
  }, []);

  useEffect(() => {
    setCurrentLayout(layout);
  }, []);

  useEffect(() => {
    const wrapperElement = document.querySelector(`.${wrapperClassName}`);
    if (!wrapperElement) {
      return;
    }
    if (currentLayout === "inline") {
      wrapperElement.classList.add("kodseged-inline");
      wrapperElement.classList.remove("kodseged-block");
    } else {
      wrapperElement.classList.add("kodseged-block");
      wrapperElement.classList.remove("kodseged-inline");
    }
  }, [currentLayout]);

  const [fileChanges, setFileChanges] = useState([]);
  useEffect(() => {
    (async () => {
      const res = await fetch(fileChangesURL).catch(console.log);
      if (res && !res.ok) {
        return;
      }
      const headers = Object.fromEntries(Array.from(res.headers.entries()));
      let fileChanges = [];
      if (headers["content-type"] === "application/xml") {
        fileChanges = await parseXMLfromRequest(res);
      }

      if (headers["content-type"] === "application/json") {
        fileChanges = await res.json().catch(console.log);
      }

      if (!fileChanges?.length) {
        return;
      }
      setFileChanges(fileChanges.map((change) => ({ ...change, languageName: toExtension(change.fileName) })));
    })();
  }, []);

  async function parseXMLfromRequest(res) {
    const txt = await res.text();

    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(txt, "text/xml");
    return Array.from(xmlDoc.firstChild.children)
      .map((item) =>
        Array.from(item.children).reduce(
          (acc, element) => ({
            ...acc,
            [element.localName]: element.innerHTML,
          }),
          {}
        )
      )
      .map((item) => ({ ...item, timestamp: moment(item.timestamp, "mm:ss").diff(moment().startOf("day"), "seconds") }))
      .map((item) => ({ ...item, content: item.content.replace("&gt;", ">").replace("&lt;", "<") }))
      .map((item) => ({ ...item, content: item.content.replace("<![CDATA[\n", "").replace("]]>", "") }));
  }

  const containerRef = useRef();
  const currentTime = useRef(0);

  if (!fileChanges.length) {
    return <div className="text-center">Betöltés...</div>;
  }

  return (
    <Fragment>
      <div className="container-fluid">
        <div className="row">
          <div
            ref={containerRef}
            className={(currentLayout === "inline" ? "col-md-8" : "col-md-12") + " p-0 pr-1 border-right border-dark"}
            style={{ backgroundColor: "rgb(35, 36, 31)" }}
          >
            <Vimeo
              video={videoId}
              responsive={true}
              onReady={(e) => {
                video.current = e.element;
                setInit(1);
              }}
              onTimeUpdate={(e) => {
                currentTime.current = e.seconds;
              }}
              onSeeked={(e) => {
                setVideoStateChanged((prev) => prev + 1);
              }}
            />
          </div>

          {isInit ? (
            <Renderer
              containerRef={containerRef}
              fontSize={fontSize}
              channel={channel}
              isVisible={isVisible}
              className="w-100"
              fileChanges={fileChanges}
              videoReference={video}
              videoStateChanged={videoStateChanged}
              currentLayout={currentLayout}
              setCurrentLayout={setCurrentLayout}
              setExplicitelyActive={setExplicitelyActive}
              explicitelyActive={explicitelyActive}
              getCurrentTime={() => currentTime.current}
            />
          ) : (
            Loading(currentLayout)
          )}
        </div>
        <ControlButtons
          setFontSize={setFontSize}
          currentLayout={currentLayout}
          setCurrentLayout={setCurrentLayout}
          isVisible={isVisible}
          setVisible={setVisible}
          explicitelyActive={explicitelyActive}
          setExplicitelyActive={setExplicitelyActive}
          channel={channel}
        />
      </div>
    </Fragment>
  );
}
