import React, { createRef, Fragment, useEffect, useReducer, useRef, useState } from "react";
import InputRange from "react-input-range";
import Highlight from "react-highlight";
import { toExtension } from "./App";
const gifFrames = require("gif-frames");

export function GifWithAssistantApp({ fileChangesURL, gifUrl, showFrameNumber, layout }) {
  const [fileChanges, setFileChanges] = useState([]);
  const [isPending, setPending] = useState(true);
  const [frames, setFrames] = useState([]);
  const rawFrames = useRef([]);
  const total = useRef(1);

  useEffect(() => {
    (async () => {
      setPending(true);
      const rawFrames1 = await gifFrames({ url: gifUrl, frames: "all", outputType: "canvas", cumulative: true });
      setPending(false);
      total.current = rawFrames1.length;
      rawFrames.current = rawFrames1;
      dispatch({ type: "UPDATE" });
    })();
  }, []);

  let calcCountReducer = (state, action) => {
    switch (action.type) {
      case "UPDATE":
        return { ...state, calcCount: state.calcCount + 1 };
      default:
        throw new Error("Invalid Action Type");
    }
  };
  let [state, dispatch] = useReducer(calcCountReducer, { calcCount: 0 });

  useEffect(() => {
    if (!rawFrames.current[0]) {
      return;
    }

    setFrames((prev) => {
      if (!rawFrames.current[0]) {
        return prev;
      }

      return prev.concat([rawFrames.current[0].getImage()]);
    });
    rawFrames.current.shift();
    setTimeout(() => {
      dispatch({ type: "UPDATE" });
    }, 0);
  }, [state.calcCount]);

  useEffect(() => {
    (async () => {
      const res = await fetch(fileChangesURL).catch(console.log);

      if (!res.ok) {
        return;
      }
      const txt = await res.text();

      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(txt, "text/xml");
      const fileChanges = Array.from(xmlDoc.firstChild.children)
        .map((item) =>
          Array.from(item.children).reduce(
            (acc, element) => ({
              ...acc,
              [element.localName]: element.localName === "frame" ? parseInt(element.innerHTML) : element.innerHTML,
            }),
            {}
          )
        )
        .map((item) => ({ ...item, content: item.content.replace("&gt;", ">").replace("&lt;", "<") }));

      setFileChanges(fileChanges.map((change) => ({ ...change, languageName: toExtension(change.fileName) })));
    })();
  }, []);

  if (isPending) {
    return (
      <div className="d-flex flex-column align-items-center justify-content-center p-5 bg-light">
        <div className="row mb-2 text-danger">
          <div className="spinner-border" role="status">
            <span className="sr-only">Betöltés...</span>
          </div>
        </div>
        <div className="row text-dark">
          <strong>Betöltés...</strong>
        </div>
      </div>
    );
  }

  return frames.length > 0 ? (
    <Renderer
      frames={frames}
      fileChanges={fileChanges}
      showFrameNumber={showFrameNumber}
      layout={layout}
      total={total.current}
      calcCount={state.calcCount}
    />
  ) : (
    ""
  );
}

function Renderer({ frames, fileChanges, showFrameNumber, layout, total, calcCount }) {
  const canvas = createRef();
  const [state, setState] = useState(0);
  const parentRef = useRef();
  const [parentHeight, setParentHeight] = useState(0);
  const [isInitialized, setInitialized] = useState(false);

  useEffect(() => {
    setParentHeight(parentRef.current.offsetHeight);
  }, [parentRef.current]);

  useEffect(() => {
    if (!canvas.current || isInitialized) {
      return;
    }

    const width = parentRef.current.offsetWidth;
    const img = frames[0];
    const scale = width / img.width < 1 ? width / img.width : 1;
    canvas.current.width = img.width * scale;
    canvas.current.height = img.height * scale;
    canvas.current.getContext("2d").scale(scale, scale);
    canvas.current.getContext("2d").drawImage(img, 0, 0);
    setInitialized(true);
  }, [canvas]);

  useEffect(() => {
    if (!frames[state]) {
      return;
    }
    const img = frames[state];
    const width = parentRef.current.offsetWidth;
    const scale = width / img.width < 1 ? width / img.width : 1;
    canvas.current.width = img.width * scale;
    canvas.current.height = img.height * scale;
    canvas.current.getContext("2d").scale(scale, scale);
    canvas.current.getContext("2d").drawImage(img, 0, 0);
  }, [state]);

  if (layout === "inline") {
    return (
      <div className="row">
        <div className="col-md-7 p-0 mb-5" ref={parentRef}>
          <canvas ref={canvas} />
          <div className="main-scroller">
            {total - 1 > 0 ? (
              <InputRange
                formatLabel={(num) => (showFrameNumber ? num : "")}
                minValue={0}
                maxValue={total - 1}
                value={state}
                onChange={(frameNumber) => {
                  if (frameNumber > calcCount) {
                    return;
                  }
                  setState(frameNumber);
                }}
              />
            ) : (
              ""
            )}
          </div>
          <div className="buffer-indicator-track">
            <InputRange
              disabled={true}
              formatLabel={() => ""}
              minValue={0}
              maxValue={total + 1}
              value={calcCount}
              onChange={() => {}}
            />
          </div>
        </div>
        <div className="col-md-5 p-0 m-0">
          {parentRef.current ? <CodeRenderer fileChanges={fileChanges} frame={state} height={parentHeight} /> : ""}
        </div>
      </div>
    );
  }

  return frames.length !== undefined ? (
    <Fragment>
      <div className="text-center mb-5" ref={parentRef}>
        <canvas ref={canvas} />
        <div className="main-scroller">
          {total - 1 > 0 ? (
            <InputRange
              formatLabel={(num) => (showFrameNumber ? num : "")}
              minValue={0}
              maxValue={total - 1}
              value={state}
              onChange={(frameNumber) => {
                if (frameNumber > calcCount) {
                  return;
                }
                setState(frameNumber);
              }}
            />
          ) : (
            ""
          )}
        </div>
        <div className="buffer-indicator-track">
          <InputRange
            disabled={true}
            formatLabel={() => ""}
            minValue={0}
            maxValue={total + 1}
            value={calcCount}
            onChange={() => {}}
          />
        </div>
      </div>
      {parentRef.current ? <CodeRenderer fileChanges={fileChanges} frame={state} height={parentHeight} /> : ""}
    </Fragment>
  ) : (
    ""
  );
}

function CodeRenderer({ fileChanges, frame, height }) {
  const files = useRef({});
  const [renderedFiles, setRenderedFiles] = useState({});
  const [active, setActive] = useState("");
  const [explicitelyActive, setExplicitelyActive] = useState("");

  useEffect(() => {
    setExplicitelyActive("");
    const fileNames = Object.keys(fileChanges.reduce((acc, cr) => ({ ...acc, [cr.fileName]: cr }), {}));

    let ret = {};
    for (let fileName of fileNames) {
      const items = fileChanges.filter((change) => change.fileName === fileName).filter((item) => item.frame < frame);
      if (!items.length) {
        continue;
      }
      const item = items.pop();
      ret[fileName] = item;
    }

    files.current = ret;
    let latestChangedFileName;
    let latestFrame;
    for (let item of Object.values(ret)) {
      if (item.frame < latestFrame) {
        continue;
      }
      latestFrame = item.frame;
      latestChangedFileName = item.fileName;
    }
    setActive(latestChangedFileName);
  }, [frame]);

  const currentlyActiveItem = explicitelyActive ? explicitelyActive : active;
  return (
    <div className="col-md-12 p-0 overflow-auto" style={{ height: height, backgroundColor: "rgb(35, 36, 31)" }}>
      {Object.entries(files.current).map(([fileName, file]) => (
        <div
          key={fileName}
          className="text-center p-2"
          style={{
            backgroundColor: currentlyActiveItem === fileName ? "black" : "rgb(35, 36, 31)",
            color: currentlyActiveItem === fileName ? "white" : "grey",
            width: parseInt(100 / Object.values(files.current).length) + "%",
            borderBottom: "1px solid lightgrey",
            display: "inline-block",
            cursor: "pointer",
          }}
          onClick={() => {
            if (active === fileName && !explicitelyActive) {
              return;
            }
            setExplicitelyActive(fileName);
          }}
        >
          {fileName}
        </div>
      ))}
      {Object.entries(files.current)
        .filter(([fileName, file]) => currentlyActiveItem === fileName)
        .map(([fileName, file]) => (
          <div key={fileName} className="font-family-monospace">
            <Highlight className={file.languageName}>{file.content}</Highlight>
          </div>
        ))}
    </div>
  );
}
