import React, { Fragment, useEffect, useRef, useState } from "react";
import { toExtension } from "./App";
import Highlight from "react-highlight";
import Youtube from "react-youtube";
import "./App.css";
import { isMobile } from "react-device-detect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCode,
  faColumns,
  faExpandArrowsAlt,
  faEye,
  faEyeSlash,
  faMinus,
  faPlus,
  faTextWidth,
} from "@fortawesome/free-solid-svg-icons";
import ReactTooltip from "react-tooltip";

export function YoutubeWithAssistantApp({ fileChangesURL, videoId, layout, wrapperClassName }) {
  const video = useRef();
  const [isInit, setInit] = useState();
  const [videoStateChanged, setVideoStateChanged] = useState(0);
  const [currentLayout, setCurrentLayout] = useState();
  const [explicitelyActive, setExplicitelyActive] = useState("");
  const [isVisible, setVisible] = useState(false);
  const [fontSize, setFontSize] = useState(16);

  let channel;
  try {
    channel = new BroadcastChannel("codeAssistant");
  } catch {
    channel = {
      onmessage: () => {},
      postMessage: () => {},
      close: () => {},
      noop: true,
    };
  }
  React.useMemo(() => channel, []);

  React.useEffect(() => {
    channel.onmessage = (message) => {
      if (message.data.event === "clientClosed") {
        setVisible(false);
      }
    };
    return channel.close;
  }, []);

  useEffect(() => {
    setCurrentLayout(layout);
  }, []);

  useEffect(() => {
    const wrapperElement = document.querySelector(`.${wrapperClassName}`);
    if (!wrapperElement) {
      return;
    }
    if (currentLayout === "inline") {
      wrapperElement.classList.add("kodseged-inline");
      wrapperElement.classList.remove("kodseged-block");
    } else {
      wrapperElement.classList.add("kodseged-block");
      wrapperElement.classList.remove("kodseged-inline");
    }
  }, [currentLayout]);

  const [fileChanges, setFileChanges] = useState([]);
  useEffect(() => {
    (async () => {
      const res = await fetch(fileChangesURL).catch(console.log);
      if (res && !res.ok) {
        return;
      }
      const fileChanges = await res.json().catch(console.log);
      if (!fileChanges.length) {
        return;
      }
      setFileChanges(fileChanges.map((change) => ({ ...change, languageName: toExtension(change.fileName) })));
    })();
  }, []);

  const containerRef = useRef();

  if (!fileChanges.length) {
    return <div className="text-center">Betöltés...</div>;
  }

  return (
    <Fragment>
      <div className="container-fluid">
        <div className="row">
          <div ref={containerRef} className={(currentLayout === "inline" ? "col-md-8" : "col-md-12") + " p-0 pr-1"}>
            <Youtube
              opts={{
                width: "100%",
              }}
              videoId={videoId}
              onReady={(e) => {
                video.current = e.target;
                setInit(1);
              }}
              onStateChange={(e) => {
                if (e.data !== 2) {
                  return;
                }
                setVideoStateChanged((prev) => prev + 1);
              }}
            />
          </div>

          {isInit ? (
            <Renderer
              containerRef={containerRef}
              fontSize={fontSize}
              channel={channel}
              isVisible={isVisible}
              className="w-100"
              fileChanges={fileChanges}
              videoStateChanged={videoStateChanged}
              currentLayout={currentLayout}
              setCurrentLayout={setCurrentLayout}
              setExplicitelyActive={setExplicitelyActive}
              explicitelyActive={explicitelyActive}
              getCurrentTime={() => video.current.getCurrentTime()}
            />
          ) : (
            Loading(currentLayout)
          )}
        </div>
        <ControlButtons
          setFontSize={setFontSize}
          currentLayout={currentLayout}
          setCurrentLayout={setCurrentLayout}
          isVisible={isVisible}
          setVisible={setVisible}
          explicitelyActive={explicitelyActive}
          setExplicitelyActive={setExplicitelyActive}
          channel={channel}
        />
      </div>
    </Fragment>
  );
}

export function ControlButtons({
  setFontSize,
  currentLayout,
  setCurrentLayout,
  isVisible,
  setVisible,
  explicitelyActive,
  setExplicitelyActive,
  channel,
}) {
  return (
    <div className="row" style={{ backgroundColor: "rgb(35, 36, 31)" }}>
      {currentLayout === "inline" ? <div className="col-md-8"></div> : ""}
      <div className={currentLayout === "inline" ? "col-md-4 border-top border-dark" : "col-md-12"}>
        {!isMobile && !isVisible ? (
          <button
            className="btn btn-sm btn-warning d-inline-block mr-2"
            onClick={() => {
              if (currentLayout === "inline") {
                setCurrentLayout("block");
              } else {
                setCurrentLayout("inline");
              }
            }}
            data-tip="Nézet váltása"
          >
            <FontAwesomeIcon icon={faColumns}></FontAwesomeIcon>
            <ReactTooltip place="top" type="dark" effect="float" />
          </button>
        ) : (
          ""
        )}

        <button
          className="btn btn-sm btn-warning d-inline-block mr-2"
          onClick={() => {
            setVisible((prev) => !prev);
          }}
          data-tip={isVisible ? "Megjelenítés" : "Elrejtés"}
        >
          <FontAwesomeIcon icon={isVisible ? faEye : faEyeSlash}></FontAwesomeIcon>
          <ReactTooltip place="top" type="dark" effect="float" />
        </button>
        {!isVisible ? (
          <Fragment>
            {!channel.noop ? (
              <button
                className="btn btn-sm btn-warning d-inline-block mr-2"
                onClick={() => {
                  setVisible(true);
                  Object.assign(document.createElement("a"), {
                    target: "_blank",
                    href: "/kodseged-kliens?isFullPageClientApp=1",
                  }).click();
                }}
                data-tip="Teljes képernyő"
              >
                <FontAwesomeIcon icon={faExpandArrowsAlt}></FontAwesomeIcon>
                <ReactTooltip place="top" type="dark" effect="float" />
              </button>
            ) : (
              ""
            )}
            <button
              className="btn btn-sm btn-outline-warning m-1"
              onClick={() => {
                setFontSize((prev) => prev - 1);
              }}
              data-tip="Betűméret csökkentése"
            >
              <small>
                <FontAwesomeIcon icon={faMinus}></FontAwesomeIcon>
              </small>
              <FontAwesomeIcon icon={faTextWidth}></FontAwesomeIcon>
              <ReactTooltip place="top" type="dark" effect="float" />
            </button>
            <button
              className="btn btn-sm btn-outline-warning m-1"
              onClick={() => {
                setFontSize((prev) => prev + 1);
              }}
              data-tip="Betűméret növelése"
            >
              <small>
                <FontAwesomeIcon icon={faPlus}></FontAwesomeIcon>
              </small>
              <FontAwesomeIcon icon={faTextWidth}></FontAwesomeIcon>
              <ReactTooltip place="top" type="dark" effect="float" />
            </button>
            <div className="d-inline-block">
              {explicitelyActive ? (
                <button
                  className="btn btn-sm btn-outline-success"
                  onClick={() => {
                    if (!explicitelyActive) {
                      return;
                    }
                    setExplicitelyActive("");
                  }}
                  data-tip="Követés bekapcsolása"
                >
                  <FontAwesomeIcon icon={faCode}></FontAwesomeIcon>
                  <ReactTooltip place="top" type="dark" effect="float" />
                </button>
              ) : (
                ""
              )}
            </div>
          </Fragment>
        ) : (
          ""
        )}
      </div>
    </div>
  );
}

export function Loading(layout) {
  return (
    <div className={layout === "inline" ? "col-md-4" : "col-md-12" + " text-center bg-light"}>
      <div className="d-flex justify-content-center center">
        <div className="spinner-border text-danger mr-2" role="status">
          <span className="sr-only">Betöltés...</span>
        </div>
      </div>
    </div>
  );
}

export function Renderer({
  containerRef,
  channel,
  fontSize,
  fileChanges,
  videoStateChanged,
  currentLayout,
  explicitelyActive,
  setExplicitelyActive,
  isVisible,
  getCurrentTime,
}) {
  const files = useRef({});
  const [renderedFiles, setRenderedFiles] = useState({});
  const [active, setActive] = useState("");

  useEffect(() => {
    const fileNames = Object.keys(fileChanges.reduce((acc, cr) => ({ ...acc, [cr.fileName]: cr }), {}));

    const currentTime = getCurrentTime();
    let ret = {};
    for (let fileName of fileNames) {
      const items = fileChanges
        .filter((change) => change.fileName === fileName)
        .filter((item) => item.timestamp < currentTime);
      if (!items.length) {
        continue;
      }
      const item = items.pop();
      ret[fileName] = item;
    }

    files.current = ret;
    let latestChangedFileName;
    let latestTimestamp;
    for (let item of Object.values(ret)) {
      if (item.timestamp < latestTimestamp) {
        continue;
      }
      latestTimestamp = item.timestamp;
      latestChangedFileName = item.fileName;
    }

    setActive(latestChangedFileName);
    setExplicitelyActive("");
  }, [videoStateChanged]);

  useEffect(() => {
    tick();
    setTimeout(() => {
      channel.postMessage({
        event: "init",
        payload: {
          files: files.current,
          active: active,
        },
      });
    }, 100);
  }, [videoStateChanged]);

  useEffect(() => {
    channel.onmessage = (message) => {
      if (message.data.event === "clientConnected") {
        channel.postMessage({
          event: "init",
          payload: {
            files: files.current,
            active: active,
          },
        });
      }
    };
  }, []);

  useEffect(() => {
    setTimeout(() => {
      channel.postMessage({
        event: "refresh",
        payload: {
          files: files.current,
          active: active,
        },
      });
    }, 100);
  }, [renderedFiles]);

  useEffect(() => {
    const interval = setInterval(tick, 50);

    return () => {
      clearInterval(interval);
    };
  }, []);

  function tick() {
    const currentTime = getCurrentTime();
    const items = fileChanges.filter((item) => item.timestamp < currentTime);
    if (!items.length) {
      return;
    }
    const item = items.pop();
    if (files.current[item.fileName]?.content === item.content) {
      return;
    }

    files.current = {
      ...files.current,
      [item.fileName]: item,
    };

    setRenderedFiles(files.current);
    setActive(item.fileName);
  }

  if (isVisible) {
    return "";
  }

  const currentlyActiveItem = explicitelyActive ? explicitelyActive : active;
  return (
    <div
      className={(currentLayout === "inline" ? "col-md-4" : "col-md-12") + " p-0 "}
      style={{
        height: containerRef.current.clientHeight,
        backgroundColor: "rgb(35, 36, 31)",
      }}
    >
      {Object.entries(files.current).map(([fileName, file]) => (
        <div
          key={fileName}
          className="text-center p-2"
          style={{
            backgroundColor: currentlyActiveItem === fileName ? "black" : "rgb(35, 36, 31)",
            color: currentlyActiveItem === fileName ? "white" : "grey",
            width: parseInt(100 / Object.values(files.current).length) + "%",
            borderBottom: "1px solid lightgrey",
            display: "inline-block",
            cursor: "pointer",
          }}
          onClick={() => {
            if (active === fileName && !explicitelyActive) {
              return;
            }
            setExplicitelyActive(fileName);
          }}
        >
          {fileName}
        </div>
      ))}
      <div className="overflow-auto">
        {Object.entries(files.current)
          .filter(([fileName, file]) => currentlyActiveItem === fileName)
          .map(([fileName, file]) => (
            <div
              key={fileName}
              style={{
                height: containerRef.current.clientHeight - 30 - (explicitelyActive ? 45 : 0),
                fontSize: `${fontSize}px`,
              }}
              className="font-family-monospace"
            >
              <Highlight className={file.languageName}>{file.content}</Highlight>
            </div>
          ))}
      </div>
    </div>
  );
}
