import { faCode, faMinus, faPlus, faTextWidth, faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import Highlight from "react-highlight";
import ReactTooltip from "react-tooltip";

export function CodeAssistantClient() {
  const [files, setFiles] = useState({});
  const [active, setActive] = useState("");
  const [fontSize, setFontSize] = useState(16);
  const [explicitelyActive, setExplicitelyActive] = useState("");

  const channel = React.useMemo(() => new BroadcastChannel("codeAssistant"), []);

  React.useEffect(() => {
    channel.postMessage({ event: "clientConnected" });
    channel.onmessage = (message) => {
      const handlerFn = {
        refresh: (payload) => {
          setFiles(payload.files);
          setActive(
            payload.active && Object.keys(payload.files).includes(payload.active)
              ? payload.active
              : Object.keys(payload.files).pop()
          );
        },
        init: (payload) => {
          setFiles(payload.files);
          setActive(
            payload.active && Object.keys(payload.files).includes(payload.active)
              ? payload.active
              : Object.keys(payload.files).pop()
          );
        },
      }[message.data.event];
      if (handlerFn) {
        handlerFn(message.data.payload);
      }
    };
  }, []);

  const currentlyActiveItem = explicitelyActive ? explicitelyActive : active;
  return (
    <div
      className="col-md-12 p-0 w-100"
      style={{
        backgroundColor: "rgb(35, 36, 31)",
      }}
    >
      {Object.entries(files).map(([fileName, file]) => (
        <div
          key={fileName}
          className="text-center p-2"
          style={{
            backgroundColor: currentlyActiveItem === fileName ? "black" : "rgb(35, 36, 31)",
            color: currentlyActiveItem === fileName ? "white" : "grey",
            width: parseInt(100 / Object.values(files).length) + "%",
            borderBottom: "1px solid lightgrey",
            height: "30px",
            display: "inline-block",
            cursor: "pointer",
          }}
          onClick={() => {
            if (active === fileName && !explicitelyActive) {
              return;
            }
            setExplicitelyActive(fileName);
          }}
        >
          {fileName}
        </div>
      ))}
      <div className="overflow-auto">
        {Object.entries(files)
          .filter(([fileName, file]) => currentlyActiveItem === fileName)
          .map(([fileName, file]) => (
            <div key={fileName} className="font-family-monospace" style={{ fontSize: `${fontSize}px` }}>
              <Highlight className={file.languageName}>{file.content}</Highlight>
            </div>
          ))}
      </div>
      <div className="row p-3">
        <div className="btn-group fixed-bottom w-25">
          <button
            className="btn btn-sm btn-warning m-1"
            onClick={() => {
              setFontSize((prev) => prev - 1);
            }}
            data-tip="Betűméret csökkentése"
          >
            <small>
              <FontAwesomeIcon icon={faMinus}></FontAwesomeIcon>
            </small>
            <FontAwesomeIcon icon={faTextWidth}></FontAwesomeIcon>
            <ReactTooltip place="top" type="dark" effect="float" />
          </button>
          <button
            className="btn btn-sm btn-warning m-1"
            onClick={() => {
              setFontSize((prev) => prev + 1);
            }}
            data-tip="Betűméret növelése"
          >
            <small>
              <FontAwesomeIcon icon={faPlus}></FontAwesomeIcon>
            </small>
            <FontAwesomeIcon icon={faTextWidth}></FontAwesomeIcon>
            <ReactTooltip place="top" type="dark" effect="float" />
          </button>
          <button
            className="btn btn-sm btn-danger m-1"
            onClick={() => {
              channel.postMessage({ event: "clientClosed" });
              window.close();
            }}
            data-tip="Bezárás"
          >
            <FontAwesomeIcon icon={faTimes}></FontAwesomeIcon>

            <ReactTooltip place="top" type="dark" effect="float" />
          </button>
          {explicitelyActive ? (
            <button
              className="btn btn-sm btn-success m-1"
              onClick={() => {
                if (!explicitelyActive) {
                  return;
                }
                setExplicitelyActive("");
              }}
              data-tip="Követés bekapcsolása"
            >
              <FontAwesomeIcon icon={faCode}></FontAwesomeIcon>
              <ReactTooltip place="top" type="dark" effect="float" />
            </button>
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
}
